<template>
  <div class='redPacket'>
    <div class="redPacket-head">
      <img class="avatar" :src="codeInfo.vo.userPhoto">
    </div>
    <div class="redPacket-center">
      <span class="centent-top">{{codeInfo.vo.userCompanyName}}的红包</span>
      <span class="centent-wishWord">恭喜发财，大吉大利</span>
      <div class="centent-bottom">
        <span class="money">{{codeInfo.scanResult / 100}}</span>
        <span>元</span>
      </div>
      <div class="getSuccess">已存入零钱</div>
    </div>
    <div class="redPacket-billboard">
      <div class="redPacket-billboard-packetNum">
        <span class="title">已领取{{total}}/{{codeInfo.vo.qrCount}}个</span>
      </div>
      <div v-for="item in list" class="redPacket-billboard-item">
        <div class="user">
          <img class="avatar" :src="item.vo.customerPhoto">
          <div style="text-align: left;">
            <div>{{item.vo.customerName}}</div>
            <div>{{item.updateDate | parseTime('{m}-{d} {h}:{i}:{s}')}}</div>
          </div>
        </div>
        <div class="money">
          <span>{{item.scanResult / 100}}元</span>
        </div>
      </div>
      <div class="redPacket-billboard-bottom">
        <span v-if="isLast" @click="getMore">查看更多</span>
      </div>
    </div>
  </div>
</template>
<script>
import { getActivityScaned } from '@/api'
export default {
  props: {
    codeInfo: {
      type: Object
    }
  },
  data() {
    return {
      list: [], //数据
      showlaoding: true, //是否显示loading效果
      total: 0,
      pages: 0,
      listQuery: {
        current: 1, //页码
        size: 5,
        condition: {
          activity: this.codeInfo.activity
        }
      },
      bestLucky: ''
    }
  },
  computed: {
    isLast() {
      return this.pages > this.listQuery.current
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    getList() {
      this.showlaoding = true
      console.log(this.listQuery)
      getActivityScaned(this.listQuery).then(res => {
        const data = res.data
        if (data.code === 0 && data.msg === 'success') {
          this.list = this.list.concat(data.datas.records)
          this.pages = data.datas.pages
          this.total = data.datas.total
          this.showlaoding = false
        }
      })
    },
    getMore() {
      this.listQuery.current += 1
      this.getList()
    }
  }

}

</script>
<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.redPacket {
  height: 100%;
  width: 100%;
  background: #fff;
  position: relative;
  top: 0;
  left: 0;
  text-align: center;
  z-index: 2;
  &-head {
    position: absolute;
    top: 0;
    height: 1.8rem;
    width: 100%;
    transition: border-radius .15s;
    background-color: #d85940;
    padding: 0;
    color: #ffe2b1;
    border-radius: 0% 0% 50% 50% / 0 0 100% 100%;
    -webkit-border-radius: 0% 0% 50% 50% / 0 0 100% 100%;
    @include flex;
    @include flex-direction-column;
    @include flex-align-center;
    @include flex-pack-justify;
    .avatar {
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 0.1rem;
      padding: 2px;
      background: #fff;
      position: relative;
      top: 1.05rem;
    }
  }
  &-center {
    text-align: center;
    padding: 2.8rem 0 0.3rem 0;
    background: #f1f1f1;
    .centent-top {
      font-size: 0.38rem;
      font-weight: bold;
      display: block;
    }
    .centent-wishWord {
      font-size: 0.38rem;
      display: block;
      margin-top: 0.1rem;
    }
    .centent-bottom {
      margin-top: 0.1rem;
      font-size: 0.45rem;
      .money {
        font-size: 1.5rem;
        color: #d85940;
        font-family: Tahoma, Helvetica, Arial, '宋体', sans-serif;
      }
    }
    .getSuccess {
      font-size: 0.4rem;
      margin-top: 0.3rem;
      color: #4f6faf;
    }
  }
  &-billboard {
    width: 100%;
    background: #fff;
    padding-bottom: 0.3rem;
    &-packetNum {
      text-align: left;
      padding: 0.2rem 0.4rem;
      font-size: 0.34rem;
      color: #929292;
      border-bottom: 2px solid #f1f1f1;
    }
    &-item {
      padding: 0.3rem;
      @include flex;
      @include flex-pack-justify;
      border-bottom: 2px solid #f1f1f1;
      .user {
        @include flex;
        font-size: 0.38rem;
        .avatar {
          width: 1rem;
          height: 1rem;
          border-radius: 0.1rem;
          padding: 2px;
          background: #fff;
          margin-right: 0.2rem;
        }
      }
      .money {
        font-size: 0.36rem;
        text-align: right;
      }
    }
    &-bottom {
      padding-top: 0.3rem;
      font-size: 0.35rem;
      color: #4f6faf;
    }
  }
}

</style>



